import { AxiosResponse } from 'axios'
import getService from 'src/core/services/base'
import apiKey from 'src/core/services/base/apiKey'
import { MenuInfo } from 'src/shared/components/Menu/model'

const url = 'menus/'

export function getMenuList(): Promise<AxiosResponse<{ result_data: MenuInfo[] }>> {
  return getService({
    config: {
      url,
      method: 'get',
    },
    name: apiKey.getMenuList,
    withAccessToken: true,
  })
}
